.project-view {
    width: 100%;
    height: 90%;
    padding: 10px;
    overflow: auto;
}

@media screen and (max-width: 768px) {
    .project-view {
        display: none;
    }
}

@media screen and (min-width: 1450px) {
    .project-view {
        -ms-flex-pack: justify;
            justify-content: space-between;         
    }
}


