.sidebar-options-contact-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 25px 0 25px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 20%;
}

.sidebar-option-contact-item {
    padding: 0.5rem 0.65rem 0.5rem 0.65rem;
    border-radius: 100%;
    color: white;
    /* transition: color 0.3s ease-in-out; */
}

.sidebar-option-contact-item:hover {
    background: -webkit-gradient(linear, left top, right top, color-stop(15%, #FF5C00), to(#FFC159));
    background: -webkit-linear-gradient(left, #FF5C00 15%, #FFC159 100%);
    background: -o-linear-gradient(left, #FF5C00 15%, #FFC159 100%);
    background: linear-gradient(to right, #FF5C00 15%, #FFC159 100%);
}

/* Tablets */
@media screen 
    and (min-width: 425px)
    and (max-width: 768px) {

    .sidebar-options-contact-container {
        padding: 0 12rem 0 12rem;
    }
}

